












import Vue from 'vue'

export default Vue.extend({
    created() {
        this.$root.$refs.snackbar = this;
    },
    props: {
        timeout: {
            type: Number,
            default: 3500,
        },
    },
    data: () => ({
        snackbar: false,
        text: "",
    }),
    methods: {
        open(text: string) {
            this.snackbar = true;
            this.text = text;
        },
        close() {
            this.text = "";
            this.snackbar = false;
        },
    },
})
